$(".news-filter__show").click(function () {
    $(this).parents(".news-filter").toggleClass("news-filter--active")
})


$(document).mouseup(function (e){
    var div = $('.news-filter');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("news-filter--active")){
            $(div).removeClass("news-filter--active")
        }
    }
});


$(".news-filter__show span").html($(".news-page__link--active").html())
