function mainSlider() {
    var swiper = new Swiper('.main-slide .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        navigation: {
            nextEl: '.main-slide .swiper-button-next',
            prevEl: '.main-slide .swiper-button-prev',
        },

        on: {
            init: function () {
                slidePaginationSet(this.activeIndex+1)
            },
            slideChange: function () {
                slidePaginationSet(this.activeIndex+1)
            },
        },
        breakpoints: {
            320: {
                pagination: {
                    el: '.main-slide .swiper-pagination',
                    type: 'bullets',
                    clickable: true,
                    renderBullet: function (index, className) {
                        return `
                        <span class="${className}">${getBulletIndex(index+1)}</span>
                `
                    },
                },
            },
            1200: {
                pagination: {
                    el: '.main-slide .swiper-pagination',
                    type: 'progressbar',
                    clickable: true,
                },
            },
        },
    })

    function slidePaginationSet(current) {
        let slidesCount = $(".main-slide .swiper-slide").length
        $(".main__slider-current").html(current)
        $(".main__slider-all").html(slidesCount)
    }
}


let trustSlidersArray = []

function trustSliders() {
    $(".trust__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        trustSlidersArray[index] = new Swiper(`.trust__slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 5,
            spaceBetween: 31,
            navigation: {
                nextEl: `.trust__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.trust__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.trust__slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
                renderBullet: function (index, className) {
                    return `
                        <span class="${className}">${getBulletIndex(index+1)}</span>
                `
                },
            },
            breakpoints: {
                320: {
                    slidesPerView: "auto",
                },
                992: {
                    slidesPerView: 5,
                    spaceBetween: 0,
                },
                1200: {
                    slidesPerView: 4,
                    spaceBetween: 31,
                },
                1400: {
                    slidesPerView: 5,
                },
            },
        })
    })
}

let SlidersArray = []

function Sliders() {
    $(".slider-section").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.slider-section[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 4,
            spaceBetween: 45,
            navigation: {
                nextEl: `.slider-section[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider-section[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider-section[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
                renderBullet: function (index, className) {
                    return `
                        <span class="${className}">${getBulletIndex(index+1)}</span>
                `
                },
            },
            breakpoints: {
                320: {
                    spaceBetween: 10,
                    slidesPerView: 1,
                },
                576: {
                    slidesPerView: 2,
                },
                992: {
                    spaceBetween: 45,
                    slidesPerView: 3,
                },
                1450: {
                    slidesPerView: 4,
                },
            },
        })
    })
}

