function sliderAdvantages() {
    var swiper = new Swiper(".advantages .mySwiper", {
        spaceBetween: 7,
        slidesPerView: 6,
        freeMode: true,
        watchSlidesProgress: true,

        navigation: {
            nextEl: ".advantages .swiper-button-next",
            prevEl: ".advantages .swiper-button-prev",
        },
        pagination: {
            el: `.advantages .swiper-pagination`,
            type: 'bullets',
            clickable: true,
            renderBullet: function (index, className) {
                return `
                        <span class="${className}">${getBulletIndex(index+1)}</span>
                `
            },
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 4,
            },
            650: {
                slidesPerView: 3,
            },
            992: {
                slidesPerView: 4,
                spaceBetween: 7,
            },
            1250: {
                slidesPerView: 5,
            },
            1450: {
                slidesPerView: 6,
            },
        },
    });
}

function getBulletIndex(index) {
    if(index<10){
        return `0${index}`
    }
    return index
}




